.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $black;
    opacity: .5;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: $white;
        width: 62.5em;
        max-width: 100%;
        position: relative;
        z-index: 3;
        padding: 1.5em;
        display: inline-block;
    }
}

.close-btn{
    width: 1.5em;
    height: 1.5em;
    @include center;
    flex-shrink: 0;
    svg{
        fill: $black-64;
        width: 100%;
        height: 100%;
    }
    &:hover{
        svg{
            fill: $black;
        }
    }
}



@media screen and (max-width: 992px){
    .modal{
        padding: 0;
        &__dialog {
            height: $vh;
            overflow-y: auto;
            overflow-x: hidden;
        }
        &:after{
            display: none;
        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1em;
        }
    }
}
