.breadcrumbs {
    padding: 5em 0 .75em;
    margin-bottom: 1.5em;
    li{
        position: relative;
        display: inline;
        &:not(:first-child) {
            margin-left: .875em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                top: .1875em;
                right: 100%;
                margin-right: .25em;
                width: .625em;
                height: .625em;
                background-image: url(../img/arrow-right-black.svg);
                @include bgImgSize(100%);
            }
        }
        &:last-child{
            pointer-events: none;
            color: $black-64;
        }
    }
    a{
        font-size: .875em;
        line-height: 143%;
        letter-spacing: 0.01em;
    }
}



@media screen and (max-width: 992px){
    .breadcrumbs{
        padding: 4.5em 0 .75em;
        a{
            font-size: .8125em;
            line-height: 138%;
        }
    }
}


@media screen and (max-width: 576px){
    .breadcrumbs{
        margin-bottom: 1em;
    }
}
